.btn-group .buttondisp {
  background-color: #4CAF50; /* Green */
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  float: left;
  border: 1px solid green;
  border-radius: 25px;
}

.btn-group .buttondisp:hover {
  background-color: #3e8e41;
}
/*.buttondisp:hover { transform: scale(1.1); }*/

.buttondisp:active {
	background-color: red;
}

.clear {
	clear:both;
}